html {
  scroll-behavior: smooth;
}

#homepage {
  background: linear-gradient(45deg, #00c9ff, #92fe9d, #00c9ff, #92fe9d);
  position: relative;
}

#homepage-about-me {
  position: relative;
  min-height: 100vh;
}

#homepage-about-me button:focus {
  box-shadow: none!important
}

#image-cross-line {
  width: 100%;
  height: 2px;
  position: relative;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
}

#my-pic-circle {
  width: 210px;
  height: 210px;
  border-radius: 50%;
  background-color: white;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.425);

  transition: 0.2s;
}
#my-pic-circle:hover {
  transform: translateX(-50%) scale(1.05);
  transition: 0.5s;
}

#my-pic-circle img {
  height: 200px;
  width: 200px;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 50%;
}

#homepage-my-skills,
#homepage-my-works {
  min-height: 100vh;
}

.text-shadow {
  text-shadow: 1px 1px 25px #000000cc;
}

.text-shadow-1 {
  text-shadow: 1px 1px 5px rgb(0, 0, 0);
}

p {
  margin-bottom: 0 !important;
}

#down-arrow {
  color: rgba(255, 255, 255, 0.658);
  transition: 0.5s;
  height: 4em;
  width: 4em;
}

#down-arrow:hover {
  color: white;
  transition: 0.5s;
}


.my-works-card {
  transition: 0.2s;
}
.my-works-card:hover {
  transform: scale(1.01);
  transition: 0.5s;
}
.my-works-card p::first-letter {
  font-size: 150%;
  font-weight: 400;
}

.card-header h5 {
  font-weight: 400;
  text-align: center;
}

/********************************************************************/
/*              Scrollspy                                           */
/********************************************************************/

#scroll-spy-btn {
  position: fixed;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  padding: 0;
  margin: 0;
  width: 3rem;
  height: 3rem;
  color: #fff;
  transition: 0.2s;
}

#scroll-spy-btn:focus, #scroll-spy-btn:hover {
  box-shadow: none;
  transition: 0.2s;
}

#scroll-spy-btn:hover {
  transform: translateY(-50%) scale(1.1);
}

#scroll-spy-menu {
  position: fixed;
  right: -120px;
  top: 50%;
  transform: translateY(-50%);
  padding: 0;
  margin: 0;
  transition: 0.2s;
}

#scroll-spy-menu-items .list-group-item {
  background-color: rgba(255, 255, 255, 0.8);
  border: none;
}

#scroll-spy-menu-items .list-group-item:hover {
  cursor: pointer;
}

#scroll-spy-menu-items .active {
  background-color: rgba(0, 132, 255, 0.9);
}

/********************************************************************/
/*              Scrollspy                                           */
/********************************************************************/

#homepage-social-medias {
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0), rgba(35, 240, 230, 0.671), rgba(255, 255, 255, 0.829));
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.3);
}

#homepage-social-medias svg {
  width: 2rem;
  height: 2rem;
  transition: 0.2s;
}

#homepage-social-medias svg:hover {
  transform: scale(1.2);
  transition: 0.2s;
}

#homepage-social-medias button:focus {
  box-shadow: none;
}


/********************************************************************/
/*              Scrollbar                                           */
/********************************************************************/
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
/********************************************************************/
/*              Scrollbar                                           */
/********************************************************************/
